import React, { Component } from 'react';
import { motion } from "framer-motion";

export class Card extends Component {
    
    
    render() {
        const {id , onClick, type, enable, img} = this.props;
        const frontCls = `sides front front${id}`;
        const backCls = `sides back back${id}`;
        const delay = id > 9 ? `1.${id-9}` : `.${id}`;
        const transition = {
            ease: "easeInOut", 
            duration: .3,
            delay: delay
        }
        // const elemBack = document.getElementById(`back${id}`);
        // const elemFront = document.getElementById(`front${id}`);

        //     elemBack.style.transform= "rotateY(0deg)";
        //     elemFront.style.transform= "rotateY(1800deg)";
        // setTimeout(() => {
        //     elemBack.style.transform= "rotateY(-180deg)";
        //     elemFront.style.transform= "rotateY(0deg)";
        // }, 1000);
        return (
            <motion.div 
                initial={{ opacity:0, scale:3, rotateY:"-180deg" }} 
                transition={transition} 
                animate={{ opacity:1, scale:1 , rotateY:0}} 
                className="one">
                <button     
                    id={`front${id}`} 
                    onClick={()=>onClick(id,type)}
                    className={frontCls}
                    disabled={enable}
                >
                {/* {type} */}
                </button>
                <motion.img 
                    
                    src={img} 
                    id={`back${id}`} 
                    className={backCls} 
                    width="100%" 
                    height="100%" />
            </motion.div>
        )
    }
}

export default Card
